.page-catalog {
  position: relative;
  font-size: 19px;
  color: @white;
  font-weight: 700;
  line-height: 24px;
  background-color: @mint;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 30px;
    padding: 0 50px;
    background-color: @lightgrey-background;
  }
}

.page-catalog__list {
  margin-bottom: 31px;

  @media (min-width: @tablet-width) {
    width: 668px;
    display: grid;
    grid-template-columns: repeat(2, 334px);
    margin: 0 auto 52px;
  }

  @media (min-width: @desktop-width) {
    width: 1050px;
    grid-template-columns: repeat(2, 525px);
    margin-bottom: 54px;
  }
}

.page-catalog__item {
  box-sizing: border-box;
  background-repeat: no-repeat;

  a {
    display: block;
    box-sizing: border-box;
    width: 320px;
    min-height: 103px;
    margin: 0 auto;
    padding: 24px 160px 31px 30px;

    @media (min-width: @tablet-width) {
      padding: 25px 167px 34px 36px;
      margin: 0;
    }

    @media (min-width: @desktop-width) {
      padding: 25px 350px 34px 37px;
    }

    &:hover {
      padding: 19px 160px 41px 30px;

      @media (min-width: @tablet-width) {
        padding: 20px 167px 44px 36px;
      }

      @media (min-width: @desktop-width) {
        padding: 20px 350px 44px 37px;
      }
    }
  }

  &:nth-child(even) {
    background-color: @darkmint;

    &:hover {
      background-color: @mint-hover;
    }
  }

  &:nth-child(odd) {
    background-color: @mint;

    &:hover {
      background-color: #59d5bc;
    }
  }

  &--interior {
    background-image: url("../img/interior.svg");
    background-position: right calc(((100vw - 320px) / 2) + 27px) center;

    @media (max-width: 320px) {
      background-position: right 36px center;
    }

    @media (min-width: @tablet-width) {
      background-position: right 36px center;
    }
  }

  &--toy {
    background-image: url("../img/toy.svg");
    background-position: right calc(((100vw - 320px) / 2) + 19px) center;

    @media (max-width: 320px) {
      background-position: right 27px center;
    }

    @media (min-width: @tablet-width) {
      background-position: right 33px center;
    }
  }
}
