.main-wrapper__title {
  position: relative;
  background-color: @lightgrey-background;
  border-top: 1px solid fade(@black, 10%);

  @media (min-width: @tablet-width) {
    margin-top: -74px;
    border: none;
  }

  &--without-image {
    padding: 73px 30px;

    @media (min-width: @tablet-width) {
      padding: 100px 75px 75px;
    }

    @media (min-width: @desktop-width) {
      padding: 101px 50px 96px;
    }
  }

  &::after {
    @media (min-width: @tablet-width) {
      content: "";
      position: absolute;
      bottom: -12px;
      left: calc((100vw - 784px) / 2);
      width: 768px;
      height: 24px;
      background-image: url("../img/page-catalog-wave-tablet.svg");
    }

    @media (min-width: @desktop-width) {
      bottom: -17px;
      left: calc((100vw - 1166px) / 2);
      width: 1150px;
      height: 34px;
      background-image: url("../img/page-catalog-wave-desktop.svg");
    }
  }
}

.main-wrapper__title-image {
  padding: 72px 30px 120px;
  background: url("../img/main-title-mobile.jpg") no-repeat center -17px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/main-title-mobile@2x.jpg");
    background-size: 320px auto;
  }

  @media (min-width: @tablet-width) {
    padding: 154px 50px 372px;
    background: url("../img/main-title-tablet.jpg") no-repeat center 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/main-title-tablet@2x.jpg");
      background-size: @tablet-width auto;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 158px 50px 355px;
    background: url("../img/main-title-desktop.jpg") no-repeat center 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/main-title-desktop@2x.jpg");
      background-size: @desktop-width auto;
    }
  }
}
